html {
  font-size: 10px;
  line-height: 10px;
  font-weight: 400; }

body {
  font-size: 1.6rem;
  line-height: 2.1rem;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  color: #4a4a4a;
  overflow-x: hidden; }

body,
*,
*::before,
*::after {
  box-sizing: border-box;
  outline-color: #212e4e; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("Open Sans"), url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("Open Sans Bold"), url("../fonts/open-sans-bold.woff2") format("woff2"), url("../fonts/open-sans-bold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.wrapper {
  display: flex;
  justify-content: stretch;
  min-height: 100vh;
  position: relative; }

.wrapper__img {
  flex-basis: 50%;
  background-image: url("../img/jeans.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }
  @media (max-width: 767px) {
    .wrapper__img {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1; } }

.login {
  flex-basis: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }
  @media (max-width: 767px) {
    .login {
      flex-basis: 100%; } }

.login__overlay {
  width: 70%;
  max-width: 400px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .login__overlay {
      margin: 0;
      position: absolute;
      top: 50%;
      right: -200px;
      width: 400px;
      transform: translateY(-50%); } }
  @media (max-width: 767px) {
    .login__overlay {
      top: 0;
      left: 0;
      right: unset;
      transform: unset;
      width: 100%;
      max-width: unset;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(255, 255, 255, 0.3); } }

.login__form {
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.2); }
  @media (max-width: 767px) {
    .login__form {
      position: absolute;
      width: 80%;
      max-width: 400px; } }

.form {
  background-color: #f5f5f5; }

.form__title {
  font-size: 2rem;
  line-height: 3rem;
  font-weight: 700;
  margin: 0;
  padding: 35px 25px 25px;
  background-color: #212e4e;
  color: #ffffff;
  box-shadow: inset 0 15px 0 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.2); }

.form__list {
  list-style: none;
  margin: 0;
  padding: 50px 25px;
  display: flex;
  flex-direction: column;
  gap: 40px;
  border-bottom: 1px solid #c1c1c1; }

.form__item {
  position: relative; }

.form__item input {
  font-size: 1.6rem;
  line-height: 2rem;
  font-weight: 400;
  outline: none;
  padding: 10px;
  border: 1px solid transparent;
  border-bottom: 1px solid #212e4e;
  background-color: transparent;
  width: 100%;
  color: #212e4e; }
  .form__item input::-moz-placeholder {
    color: rgba(33, 46, 78, 0.7); }
  .form__item input::placeholder {
    color: rgba(33, 46, 78, 0.7); }
  .form__item input:hover {
    background-color: rgba(255, 255, 255, 0.5);
    transition: all 0.3 ease-in-out; }
  .form__item input:focus {
    border-color: transparent;
    box-shadow: -6px -6px 10px white, inset 6px 6px 10px rgba(74, 74, 74, 0.1), inset -6px -6px 10px white, 6px 6px 10px rgba(74, 74, 74, 0.1);
    transition: all 0.3 ease-in-out; }

.form__eye {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  border: none;
  background-color: transparent;
  background-image: url("../../img/icon-opened.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px auto;
  width: 50px;
  height: 100%;
  padding: 0;
  cursor: pointer; }
  .form__eye--visible {
    background-image: url("../../img/icon-closed.svg"); }
  .form__eye:hover {
    background-color: rgba(33, 46, 78, 0.4);
    transition: all 0.3 ease-in-out; }
  .form__eye:active {
    background-color: rgba(33, 46, 78, 0.1);
    transition: all 0.3 ease-in-out; }

.form__submit {
  display: block;
  margin: 0 0 0 auto;
  padding: 20px 25px;
  text-transform: uppercase;
  border: none;
  background-color: transparent;
  cursor: pointer; }
  .form__submit:hover {
    background-color: rgba(33, 46, 78, 0.4);
    transition: all 0.3 ease-in-out; }
  .form__submit:active {
    background-color: rgba(33, 46, 78, 0.1);
    transition: all 0.3 ease-in-out; }
